<template>
  <div class="container">
    <h2 class="text-center">帳戶管理</h2>
    <button class="btn btn-outline-secondary btn-sm mb-3" type="button" @click="$router.go(-1)">返回上一頁</button>
    <!-- 訊息提示框 -->
    <Alert
      :MODE="MSG.MODE"
      :MSGTYPE="MSG.TYPE"
      :TITLE="MSG.TITLE"
      :TEXT="MSG.TEXT"
      @alert-check="(isClick, OKorCancel) => (alertCheck = { isClick, OKorCancel })"
    />
    <div class="row">
      <div class="col-12">
        <table class="table">
          <thead>
            <tr>
              <td>UID</td>
              <td>姓名</td>
              <td>身分別</td>
              <td>選擇</td>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="item in accountData"
              :class="
                item.ident === 'Admin'
                  ? 'table-danger'
                  : item.ident === 'User'
                  ? 'table-success'
                  : item.ident === 'Register'
                  ? 'table-warning'
                  : 'table-secondary'
              "
            >
              <td>{{ item.studentID }}</td>
              <td>{{ item.name }}</td>
              <td>{{ item.ident === "Admin" ? "管理者" : item.ident === "User" ? "使用者" : item.ident === "Register" ? "未審核" : "停用" }}</td>
              <td>
                <input class="form-check-input" type="radio" name="userRadio" :value="item.studentID" v-model="selectUID" />
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="col-12 d-grid gap-2 my-2">
        <button class="btn btn-primary btn-lg" type="button" @click="selectDeal()">執行</button>
      </div>
    </div>
  </div>
</template>

<script>
import router from "@/router";
import Alert from "@/components/Alert.vue";

export default {
  name: "AccountManage",
  components: {
    Alert,
  },
  data() {
    return {
      // 訊息提示框資料容器
      MSG: {
        MODE: "",
        TYPE: "",
        TITLE: "",
        TEXT: [],
      },
      // 訊息確認[開關]
      alertCheck: false,
      // 訊息功能碼
      funcNum: -1,
      accountData: [],
      selectUID: "",
    };
  },
  methods: {
    selectDeal() {
      if (this.selectUser !== "") {
        // 取得選擇的使用者資料
        const data = this.accountData.find((item) => item.studentID === this.selectUID);
        this.$store.commit("SET_ACCOUNT_EDIT", data);
        router.push("/accountEdit");
      } else {
        // 訊息提示框
        this.alertVisible = true;
        this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
        this.funcNum = 1;
        this.MSG.TYPE = "warning"; // primary成功, warning警告, danger錯誤
        this.MSG.TITLE = "系統警告"; // 系統提示，系統警告，系統錯誤
        this.MSG.TEXT = ["請選擇使用者。"]; // 內容
      }
    },
    hideAlert() {
      this.$store.commit("SET_ALERT_VISIBLE", false);
      this.alertVisible = false;
    },
  },
  mounted() {
    // 取得帳戶資料
    this.$http
      .get("api/accountManage/getUsers")
      .then((res) => {
        this.accountData = res.data.data;
      })
      .catch((err) => {
        console.log(err);
      });
  },
  computed: {
    // alert開關連動狀態功能
    alertVisible: {
      get() {
        return this.$store.state.alertVisible;
      },
      set(value) {
        this.$store.commit("SET_ALERT_VISIBLE", value);
      },
    },
  },
  watch: {
    alertCheck: function (val) {
      if (val.isClick) {
        if (val.OKorCancel === "OK") {
          if (this.funcNum === 0) {
            return;
          } else if (this.funcNum === 1) {
            this.hideAlert();
          }
        } else if (val.OKorCancel === "Cancel") {
          this.hideAlert();
        }
      }
    },
  },
};
</script>

<style lang="scss"></style>
